import axios from 'axios'
import querystring from 'querystring'
import router from '@/router'

// 创建一个错误
function errorCreate (msg) {
  const error = new Error(msg)
  errorLog(error)
  throw error
}

// 记录和显示错误
function errorLog (error) {
  // 打印到控制台
  if (process.env.NODE_ENV === 'development') {
    console.log(error)
  }
  // 显示提示
  alert(error.message);
}

axios.defaults.withCredentials = true
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

// 创建一个 axios 实例
const service = axios.create({
  baseURL: process.env.VUE_APP_API,
  timeout: 5000 // 请求超时时间
})

// 请求拦截器
service.interceptors.request.use(
  config => {
    if('GET' === config.method.toUpperCase())
    {
      config.url += '?' + querystring.stringify(config.data)
      config.data = null;
    }
    else
    {
      config.data = querystring.stringify(config.data)
    }
    // 在请求发送之前做一些处理
    // const token = util.cookies.get('token')
    // 让每个请求携带token-- ['X-Token']为自定义key 请根据实际情况自行修改
    // config.headers['X-Token'] = token
    let sessionId = window.localStorage['sessionId'];
    if(sessionId)
    {
      config.headers['X-Session-Id'] = sessionId;
    }
    return config
  },
  error => {
    // 发送失败
    console.log(error)
    Promise.reject(error)
  }
)

// 响应拦截器
service.interceptors.response.use(
  response => {
    // dataAxios 是 axios 返回数据中的 data
    const dataAxios = response.data
    // 这个状态码是和后端约定的
    const { code } = dataAxios
    // 根据 code 进行判断
    if (code === undefined) {
      // 如果没有 status 代表这不是项目后端开发的接口 比如可能是 D2Admin 请求最新版本
      return dataAxios
    } else {
      // 有 status 代表这是一个后端接口 可以进行进一步的判断
      if(response.config['excludeCodes'] && response.config['excludeCodes'].indexOf(code) >= 0)
      {
        // 不做特别处理
        return dataAxios
      }
      else
      {
        switch (code) {
          case 0:
            // [ 示例 ] status === 0 代表没有错误
            return dataAxios
          case 1001:
            // 跳转路由
            router.push({
              name: 'home'
            })
            break;
          default:
            // 不是正确的 status
            errorCreate(`${dataAxios.message}`)
            Promise.reject(dataAxios)
            break
        }
      }
    }
  },
  error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400: error.message = '请求错误'; break
        case 401: error.message = '未授权，请登录'; break
        case 403: error.message = '拒绝访问'; break
        case 404: error.message = `请求地址出错: ${error.response.config.url}`; break
        case 408: error.message = '请求超时'; break
        case 500: error.message = '服务器内部错误'; break
        case 501: error.message = '服务未实现'; break
        case 502: error.message = '网关错误'; break
        case 503: error.message = '服务不可用'; break
        case 504: error.message = '网关超时'; break
        case 505: error.message = 'HTTP版本不受支持'; break
        default: break
      }
    }
    errorLog(error)
    return Promise.reject(error)
  }
)

export default service
